<template>
    <div class="cart-item">

        <div class="cart-delete" @click="clickDelete">
            <icon name="times" scale="1.5"></icon>
        </div>
        <div class="cart-content">
            <div class="ball-container">

                <div class="ball" v-for="ball in redBalls">
                    {{ball}}
                </div>
                <div class="ball blue">{{blueBall}}</div>

            </div>

            <div class="cart-desc">
                <span>单式{{count}}注{{count * 2}}元</span>
            </div>
        </div>
        <div class="arrow">
            <icon name="angle-right" scale="1.5"></icon>
        </div>
    </div>
</template>

<script>
    import Icon from "vue-awesome/components/Icon";

    export default {
        name: "CartItem",
        components: {Icon},
        props: {
            data: {
                required: true,
                type: Object
            }
        },
        computed: {
            redBalls() {
                return this.data.red.split(",")
            },
            blueBall() {
                return this.data.blue;
            },
            count() {
                return this.data.count
            }
        },
        methods: {
            clickDelete() {
                this.$emit("onClickDelete", this.data)
            }
        }
    }
</script>

<style scoped>
    .cart-item {
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        width: 100%;
        display: flex;
        align-items: center;
        padding: .5rem;
        border: .1rem none #33333333;
        border-bottom-style: solid;
    }

    .cart-delete {
        width: 3rem;
        height: 3rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .cart-content {
        margin-left: .5rem;
        display: flex;
        flex-direction: column;
    }

    .cart-desc {
        margin-top: .5rem;
        font-size: 1.2rem;
    }

    .ball-container {
        margin-top: .8rem;
        display: flex;
    }

    .ball {
        width: 2.5rem;
        height: 2.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        border: .12rem solid rgba(229, 28, 35, 1);
        border-radius: 1.5rem;
        margin-right: .5rem;
        color: rgba(229, 28, 35, 1);
    }

    .ball.blue {
        color: rgba(0, 0, 255, 1);
        border: .12rem solid rgba(0, 0, 255, 1);
    }

    .arrow {
        margin-left: 2rem;
    }
</style>