<template>

    <div class="root">

        <title-bar :back="true" :title="'购物车'" @onBack="clickBack"></title-bar>


        <div class="content">

            <swiper-pic></swiper-pic>

            <div v-for="cart in carts">
                <cart-item :data="cart" @onClickDelete="onDeleteCart"></cart-item>
            </div>

        </div>

        <div class="btn-pay" @click="pay">
            立即去支付{{cartsPayMoney}}元
        </div>
    </div>

</template>

<script>
    import TitleBar from "../components/TitleBar";
    import SwiperPic from "../components/SwiperPic";
    import CartItem from "../components/CartItem";

    import {mapActions, mapGetters} from 'vuex'

    export default {
        name: "CartPage",
        components: {CartItem, SwiperPic, TitleBar},
        computed: {
            ...mapGetters({
                carts: 'carts',
                cartsPayMoney: 'cartsPayMoney'
            })
        },
        created() {
            this.$store.dispatch("loadCart")
        },
        methods: {
            ...mapActions({
                cartRemove: 'cartRemove',
                cartSaveToOrder: 'cartSaveToOrder'
            }),
            clickBack() {
                this.$router.go(-1);
            },
            onDeleteCart(cart) {
                this.cartRemove(cart)
            },
            pay() {
                const self = this
                this.cartSaveToOrder(function (data) {
                    console.log(data)
                    self.$router.push({name: "orderDetail", params: {data: data.data}})
                })
            }
        }
    }
</script>

<style scoped>

    .btn-pay {
        height: 4.5rem;
        border: .1rem none #33333333;
        border-top-style: solid;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.2rem;
    }

    .btn-pay:active {
        background-color: #33333333;
        transition: all .4s;
    }

</style>